body {
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: grayscale;
  background-image: url("https://i.imgur.com/Sm6OMbb.jpg");
  background-size: cover;
  background-attachment:fixed;
}

code {
  background-color: lightgray;
}

