nav.ui.container .ui.header {
  float: left;
  margin-top: 12px;
}

nav.ui.container .ui.inverted.compact.menu {
  float: right;
}

.ui.inverted.vertical.center.aligned.segment {
  padding-top: 36px;
}

nav.ui.container .ui.inverted.compact.menu {
  /* background-color: rgb(51, 51, 51); */
  background: none;
}

nav.ui.container .ui.inverted.compact.menu .item {
  padding-bottom: 0;
}

nav.ui.container .ui.inverted.compact.menu .item {
  font-size: 16px;
  padding-left: 0px;
  padding-right: 0px;
  padding-bottom: 14px;
  padding-bottom: 0;
  margin-left: 16px;
  margin-right: 16px;
  border-radius: 0;
}

nav.ui.container .ui.inverted.compact.menu .item:hover {
  background: none;
}

nav.ui.container .ui.inverted.compact.menu .active.item {
  font-weight: bold;
  background-color: inherit;
  border-bottom-color: white;
  border-bottom-style: solid;
  border-bottom-width: 2px;
}

.ui.inverted.vertical.segment {
  height: 100vh;
  /* background-color: rgb(51, 51, 51); */
  background: none;
}

.ui.content.container {
  height: 300px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.ui.content.container h1.header {
  font-size: 36px;
}

.ui.content.container p {
  font-size: 21px;
  line-height: 1.6;
  font-weight: 350;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.ui.inverted.vertical.segment footer.ui.segment {
  height: 80px;
  width: 100%;
  position: absolute;
  bottom: 0;
  color: lightgray;
}

.ui.inverted.vertical.segment footer.ui.segment a {
  color: white;
}

.offset {
  position: absolute;
  top: 250px;
}
